import React from 'react';
import { Carousel } from 'react-bootstrap';
import {
    kotak_logo, qbera_logo, hdfc_logo, partners_bajaj,
    partners_aditya, partners_amex, partners_apollo, partners_bharti, partners_cigna,
    partners_future, partners_hdfc_ergo, partners_iffco, partners_liberty,
    partners_lic, partners_newindia, partners_oriental
 } from '../../../../assets/images';

export default class Partners extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            index: 0,
            direction: null,
        };
    }

    handleSelect = (selectedIndex, e) => {
        this.setState({
            index: selectedIndex,
            direction: e.direction,
        });
    }

    render() {
        const { index, direction } = this.state;

        return (
            <section className="bajajpartners pt40 pb40">
                <div className="container">
                    <div className="row">
                        <div className="col-md-5">
                            <div className="col-lg-12 sec-heading ">
                                <h2> We work with Indiaâ€™s top financial institutions</h2>
                                The financial services industry is transforming at a pace never seen before.
                                That's why we partner with the top Indian banks and non-banking financial institutions
                                to ensure we offer a wide variety of solutions for all your needs. At Bajaj Capital,
                                our aim is to become a one-stop destination for all your aspirations and fulfil them  with ease. 
                                            </div>
                        </div>
                        <div className="col-md-7 partnerbox">
                            <Carousel
                                activeIndex={index}
                                direction={direction}
                                onSelect={this.handleSelect}
                                controls={false}
                                indicators={true}
                            >
                                <Carousel.Item>
                                    <div className="row partner-logo">
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card">
                                                <img src={hdfc_logo} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card">
                                                <img src={kotak_logo} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card"><img src={partners_bajaj} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card"><img src={qbera_logo} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                    </div>

                                </Carousel.Item>
                                <Carousel.Item>
                                    
                                    <div className="row partner-logo">
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card"><img src={partners_newindia} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card"><img src={partners_aditya} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card"><img src={partners_amex} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card"><img src={partners_apollo} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        </div>
                                </Carousel.Item>
                                <Carousel.Item>
                                    <div className="row partner-logo">
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card">
                                                <img src={partners_bharti} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card"><img src={partners_cigna} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card">
                                                <img src={partners_future} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card"><img src={partners_hdfc_ergo} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                    </div>
                                </Carousel.Item>
                                <Carousel.Item>
                                    <div className="row partner-logo">
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card">
                                                <img src={partners_oriental} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card"><img src={partners_iffco} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card">
                                                <img src={partners_liberty} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                        <div className="col-md-6 col-sm-6 col-6">
                                            <div className="card"><img src={partners_lic} className="img-fluid" alt="axis" />
                                            </div>
                                        </div>
                                    </div>
                                </Carousel.Item>

                            </Carousel>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}